import RegisterForm from './Components/RegisterForm';
import Container from 'react-bootstrap/Container';
import { useState, useEffect } from 'react';
import userServices from './services/user';

const App = () => {
  const [username, setUsername] = useState('');
  const [password, setPassword] = useState('');
  const [fullname, setFullName] = useState('');
  const [alertMessage, setAlertMessage] = useState({ content: '', type: '' });

  const handleInputChange = (e, type) => {
    switch (type) {
      case 'username':
        setUsername(e.target.value);
        break;
      case 'fullname':
        setFullName(e.target.value);
        break;
      case 'password':
        setPassword(e.target.value);
        break;
      default:
        break;
    }
  };

  const handleSubmit = async (e) => {
    e.preventDefault();

    const newUser = {
      username,
      password,
    };
    try {
      const res = await userServices.createUser(newUser);
      setAlertMessage({
        content: `Registered successfully for ${res.username} - ${res.fullname}`,
        type: 'success',
      });
    } catch (err) {
      const errorMessage = err.response.data.error;
      setAlertMessage({ content: errorMessage, type: 'danger' });
    }
  };

  useEffect(() => {
    const effect = setTimeout(() => {
      setAlertMessage({ content: '', type: '' });
    }, 2000);

    return () => clearTimeout(effect);
  }, [alertMessage]);
  return (
    <Container
      fluid
      className="vh-100 d-flex justify-content-center align-items-center"
      style={{ backgroundColor: '#563d7c' }}
    >
      <RegisterForm
        className="mt-50%"
        handleInputChange={handleInputChange}
        username={username}
        password={password}
        fullname={fullname}
        handleSubmit={handleSubmit}
        alertMessage={alertMessage}
      />
    </Container>
  );
};

export default App;
