import axios from 'axios';
const baseUrl = '/api/users';
const getAll = async () => {
  const res = await axios.get();
  return res.data;
};

const createUser = async (user) => {
  const res = await axios.post(baseUrl, user);
  return res.data;
};

const userServices = {
  getAll,
  createUser,
};
export default userServices;
